export interface $me_elem_intf {
  dom_node: () => HTMLElement,
  dom_node_style: (p: {[prop: string]: any}) => void,
  dom_node_props: (p: {[prop: string]: any}) => void,
  dom_node_attrs: (p: {[prop: string]: any}) => void,
  dom_node_append_child: (child: HTMLElement | $me_elem_intf) => void,
  dom_node_insert_before: (child: HTMLElement | $me_elem_intf, ref: HTMLElement | $me_elem_intf) => void,
}

let _pxStyleProps: {[prop: string]: boolean}
function isPxStyleProp(prop: string) {
  if (!_pxStyleProps) {
    _pxStyleProps = {}
    const pxStyleProps = ['width', 'height']
    const sides = ['left', 'top', 'right', 'bottom']
    pxStyleProps.push(...sides)
    pxStyleProps.push('margin')
    pxStyleProps.push('padding')
    for (let i = 0, count = sides.length; i < count; i++) {
      let side = sides[i]
      side = side.charAt(0).toUpperCase() + side.slice(1)
      pxStyleProps.push('margin' + side)
      pxStyleProps.push('padding' + side)
    }
    for (let i = 0, count = pxStyleProps.length; i < count; i++) _pxStyleProps[pxStyleProps[i]] = true
  }
  return !!_pxStyleProps[prop]
}

export interface $me_elem_params {
  before?: HTMLElement | $me_elem_intf,
  elem?: string | HTMLElement,
  ns?: string,
  cls?: string,
  attrs?: {[prop: string]: any},
  style?: {[prop: string]: any},
  props?: {[prop: string]: any},
  events?: {[prop: string]: EventListener},
  events_async?: {[prop: string]: EventListener},
  events_sync?: {[prop: string]: EventListener},
  children?: $me_elem_params[],
}
export function $me_elem(
  parent: HTMLElement | $me_elem_intf,
  p?: $me_elem_params
) {
  const dom_node = p && p.elem && typeof p.elem != 'string' ?
    p.elem as HTMLElement :
    (p && p.ns && p.elem ?
      document.createElementNS(p.ns, p.elem as string) as HTMLElement :
      document.createElement(p && p.elem as string || 'div')
    )
  const style = dom_node.style
  function setStyleProp(prop: string, value: any) {
    if (Number.isFinite(value)) {
      if (prop == 'opacity') {
        value += ''
      } else if (isPxStyleProp(prop)) {
        value += 'px'
      }
    } else if (typeof value == 'boolean') {
      if (prop == 'visibility') value = value ? 'visible' : 'hidden'
    }
    if (typeof value != 'string') {
      const msg = `prop: ${prop}, value: ${value}`
      console.error(msg)
      console.trace()
      throw new Error(msg)
    } else {
      style[prop] = value
    }
  }

  if (p.attrs) for (let attr in p.attrs) dom_node.setAttribute(attr, p.attrs[attr])
  if (p.style) for (let prop in p.style) setStyleProp(prop, p.style[prop]) //style[prop] = p.style[prop]
  if (p.props) for (let prop in p.props) dom_node[prop] = p.props[prop]
  if (p.events) for (let event in p.events) dom_node.addEventListener(event, p.events[event], false)
  if (p.events_async) for (let event in p.events_async) dom_node.addEventListener(event, p.events_async[event], {passive: true})
  if (p.events_sync) for (let event in p.events_sync) dom_node.addEventListener(event, p.events_sync[event], {passive: false})

  if (p && p.cls) dom_node.classList.add(p.cls)
  if (parent) {
    // console.log(p && p.before)
    let beforeNode: HTMLElement
    if (p && p.before) {
      const before = p.before
      beforeNode = !(before as $me_elem_intf).dom_node ? before as HTMLElement : (before as $me_elem_intf).dom_node()
    }
    if ((parent as $me_elem_intf).dom_node) {
      if (!beforeNode) {
        (parent as $me_elem_intf).dom_node_append_child(dom_node)
      } else {
        (parent as $me_elem_intf).dom_node_insert_before(dom_node, beforeNode)
      }
    } else {
      if (!beforeNode) {
        (parent as HTMLElement).appendChild(dom_node)
      } else {
        (parent as HTMLElement).insertBefore(dom_node, beforeNode)
      }
    }
  }
  let _raf : number
  let _styleProps = {} as {[prop: string]: any}
  let _nodeProps = {} as {[prop: string]: any}
  let _nodeAttrs = {} as {[prop: string]: any}
  function applyUpdates() {
    if (_raf === void 0) _raf = requestAnimationFrame(function () {
      _raf = void 0
      for (let prop in _styleProps) {
        setStyleProp(prop, _styleProps[prop])
        // if (prop == 'width') console.warn(prop); else console.log(prop)
        delete _styleProps[prop]
      }
      for (let prop in _nodeProps) {
        dom_node[prop] = _nodeProps[prop]
        delete _nodeProps[prop]
      }
      for (let attr in _nodeAttrs) {
        dom_node.setAttribute(attr, _nodeAttrs[attr])
        delete _nodeAttrs[attr]
      }
      // console.log(document.getElementById('search_params').getBoundingClientRect().bottom - document.getElementById('search_result').getBoundingClientRect().top)
    })
  }
  const self = {
    dom_node() {
      return dom_node
    },
    dom_node_style(styleProps: {[prop: string]: any}, immediatly = false) {
      if (immediatly) throw new Error('TODO')
      for (let prop in styleProps) _styleProps[prop] = styleProps[prop]
      applyUpdates()
    },
    dom_node_props(nodeProps: {[prop: string]: any}, immediatly = false) {
      if (immediatly) {
        for (let prop in nodeProps) dom_node[prop] = nodeProps[prop]
      } else {
        for (let prop in nodeProps) _nodeProps[prop] = nodeProps[prop]
        applyUpdates()
      }
      // for (let prop in nodeProps) _nodeProps[prop] = nodeProps[prop]
      // applyUpdates()
    },
    dom_node_attrs(nodeAttrs: {[prop: string]: any}, immediatly = false) {
      if (immediatly) {
        for (let attr in nodeAttrs) dom_node.setAttribute(attr, nodeAttrs[attr])
      } else {
        for (let attr in nodeAttrs) _nodeAttrs[attr] = nodeAttrs[attr]
        applyUpdates()
      }
    },
    dom_node_append_child(child: HTMLElement | $me_elem_intf) {
      const child_node = (child as $me_elem_intf).dom_node ? (child as $me_elem_intf).dom_node() : child as HTMLElement
      dom_node.appendChild(child_node)
      return self
    },
    dom_node_insert_before(child: HTMLElement | $me_elem_intf, ref: HTMLElement | $me_elem_intf) {
      const child_node = (child as $me_elem_intf).dom_node ? (child as $me_elem_intf).dom_node() : child as HTMLElement
      const ref_node = (ref as $me_elem_intf).dom_node ? (ref as $me_elem_intf).dom_node() : ref as HTMLElement
      dom_node.insertBefore(child_node, ref_node)
      return self
    }
  }
  if (p.children) for (let i in p.children ) $me_elem(self, p.children[i])
  return self
}

