export function $me_word_plural(count : number, word1 : string, word2_4 : string, word5more? : string) {
  if (word5more === undefined) { word5more = word2_4 }
  let result = word5more
  const decimal = Math.floor(count / 10) % 10
  if (decimal != 1) {
    const unit = count % 10
    if (unit == 1) {
      result = word1
    } else if ( unit >= 2 && unit <= 4 ) {
      result = word2_4
    }
  }
  return result
}

