namespace $.$$ {
// ============================================================================

export const enum $me_ctx_halign { center, left, right }

const period = '...'
export function $me_ctx_text_render(ctx: any, text: string, ctx_x: number, ctx_w: number, ctx_padding: number, ctx_y: number, align = $me_ctx_halign.center) {
  let w = ctx.measureText(text).width
  const w_max = ctx_w - ctx_padding * 2
  if (w <= w_max) {
    if (align === $me_ctx_halign.center) {
      ctx.fillText(text, ctx_x + (ctx_w - w) / 2, ctx_y)
    } else if (align == $me_ctx_halign.left) {
      ctx.fillText(text, ctx_x + ctx_padding, ctx_y)
    } else {
      throw new Error('TODO')
    }
  } else {
    let len = text.length
    const ctxPeriodWidth = ctx.measureText(period).width
    while (len && (w = ctx.measureText(text.slice(0, len)).width) > w_max - ctxPeriodWidth) {
      len--
    }
    if (len) {
      text = text.slice(0, len)
      ctx.fillText(text, ctx_x + ctx_padding, ctx_y)
      ctx.fillText(period, ctx_x + ctx_padding + ctx.measureText(text).width, ctx_y)
    } else {
      console.error('TODO')
      throw new Error('TODO') // use .rect(), clip(), was_clip
    }
  }
}

// ============================================================================
}
